import react, { useState } from "react";
import profile from "../Images/my-profile.PNG";
import { Link } from "react-router-dom";

const Nav = () => {
  let Links = [
    { name: "Home", link: "/" },
    { name: "About Me", link: "/About-Me" },
    { name: "Blog's", link: "/Blogs" },
    { name: "Projects", link: "/Projects" },
  ];
  let [open, setOpen] = useState(false);
  return (
    <header className="shadow-md w-full relative top-0 left-0">
      <div className="md:flex items-center justify-between bg-gray-900 py-4 md:px-10 px-7">
        <div
          className="font-bold text-xl cursor-pointer flex items-center
    text-lime-500 font-mono"
        >
          <span>
            <img class="h-12 w-12" src={profile} alt="Logo" />
          </span>
          <Link to="/">Hari Om Swarup S A</Link>
        </div>

        <div
          onClick={() => setOpen(!open)}
          className="text-3xl absolute right-8 top-6 cursor-pointer md:hidden"
        >
          <span className="text-white">
            <ion-icon name={open ? "close" : "menu"}></ion-icon>
          </span>
        </div>

        <ul
          className={`z-10 md:flex md:items-center md:pb-0 pb-12 absolute md:static bg-gray-900 md:z-auto left-0 w-full md:w-auto md:pl-0 pl-9 transition-all duration-500 ease-in ${
            open ? "top-20 " : "top-[-490px]"
          }`}
        >
          {Links.map((link) => (
            <li key={link.name} className="md:ml-8 text-xl md:my-0 my-7">
              <Link
                to={link.link}
                className="font-mono text-lime-500 hover:text-blue-600 duration-500"
              >
                {link.name}
              </Link>
            </li>
          ))}
        </ul>
      </div>
    </header>
  );
};

export default Nav;

