import React from "react";
import Project_card from "./Project-card";
// import Headings from "./Heading";
import bl1 from "../Images/CubeRenderArt-Blender-Cycles.png";
import bl2 from "../Images/Lightning Spinning - Blender - Eevee.png";
import bl3 from "../Images/Last Space Man Hope - Blender - Eevee.png";
import bl4 from "../Images/Audio Waves Sync - Blender - Eevee.png";
import bl5 from "../Images/Dancing People - Blender - Eevee.png";
import p1 from "../Images/Shooting Game - Python - Pygame.png";

function Projects() {
  return (
    <>
      {/* <Headings name="Project's" /> */}
      <section class="text-gray-600 font-mono">
        <div class="container px-5 py-12 mx-auto">
          <div class="flex flex-wrap -m-4">
            <Project_card
              view={"See Image"}
              content={"Geometry Nodes Abstract Model & Environment in Blender"}
              image={bl1}
              link={
                "https://drive.google.com/file/d/1zGFLSeyWvwaZeywFoPpqCdqMeCXA9evt/view?usp=sharing"
              }
              title={"Abstract Cube Art"}
              category={"BLENDER ART"}
              alt={"Cube"}
            />
            <Project_card
              view={"Watch Clip"}
              content={"Abstract Tentacle Animation in Eevee Blender"}
              image={bl2}
              link={
                "https://drive.google.com/file/d/1lbmQ_P1w-nOFRlXDcIqFrn9uZ1KEXtIi/view?usp=sharing"
              }
              title={"Spinning Lightning Art"}
              category={"BLENDER ART"}
              alt={"Abstract"}
            />
            <Project_card
              view={"On Repo"}
              content={
                "A simple shooting game made from pygame module, give it a try!, and score high"
              }
              image={p1}
              link={"https://github.com/hariomswarupsa/Shooting-Game"}
              title={"Shooting Game"}
              category={"PYTHON PYGAME"}
              alt={"Shooting"}
            />
            <Project_card
              view={"Watch Clip"}
              content={
                "An Easy Blender Envirnoment Design, related to space theme"
              }
              image={bl3}
              link={
                "https://drive.google.com/file/d/15mIwR7XyCAod7uB_N9Xbux2yeDYTXBBa/view?usp=sharing"
              }
              title={"Last Hope Space Man"}
              category={"BLENDER ART"}
              alt={"SpaceMan"}
            />
            <Project_card
              view={"Watch Clip"}
              content={
                "Easy Stylized Characters Dancing animation Loop in Eevee"
              }
              image={bl5}
              link={
                "https://drive.google.com/file/d/1MlQ2KdWfG1THTladL2Qmhx-mLxnz4BNH/view?usp=sharing"
              }
              title={"Dance Art"}
              category={"BLENDER ART"}
              alt={"Dance"}
            />
            <Project_card
              view={"Watch Video"}
              content={
                "Audio Visualizer, similar to Trap Nation, with Heat Waves Music"
              }
              image={bl4}
              link={
                "https://drive.google.com/file/d/1hukrJVsYfXO_DAL6JGFZI9lqg5RUbK6a/view?usp=sharing"
              }
              title={"Heat Waves - Wave Sync"}
              category={"BLENDER ART"}
              alt={"Audio Sync"}
            />
          </div>
        </div>
      </section>
    </>
  );
}

export default Projects;
