import React from "react";
import Blog_card from "./Blog-card";

function Blog_index() {
  return (
    <>
      <section className="pt-2 px-3 py-3 text-gray-400 font-mono overflow-hidden">
        <Blog_card
          link={"/Blogs/VSCode"}
          content={
            "In this blog you will learn how to install VS code on your system. Which will help you to code programs and develop applications."
          }
          view={"Read Blog"}
          category={"DEVELOPER'S BASIC"}
          title={"VS Code Installation"}
          date={"14 April 2022"}
        />
      </section>
    </>
  );
}

export default Blog_index;
