import PreviousMap from "postcss/lib/previous-map";
import React from "react";

function Project_card(props) {
  return (
    <div class="pl-4 pr-4 pb-4 md:w-1/3">
      <div class="h-full border-2 border-gray-900 glass-effect bg-transparent-dark-gray border-opacity-60 rounded-lg overflow-hidden">
        <img
          class="lg:h-60 md:h-36 w-full object-cover object-center"
          src={props.image}
          alt={props.altname}
        />
        <div class="p-6">
          <h2 class="tracking-widest text-xs title-font font-medium text-blue-200 underline underline-offset-2 mb-1">
            {props.category}
          </h2>
          <h1 class="title-font text-lg font-medium text-lime-500 mb-3">
            {props.title}
          </h1>
          <p class="leading-relaxed mb-3 text-white">{props.content}</p>
          <div class="flex items-center flex-wrap ">
            <a
              target="_blank"
              class="transition-all duration-100 ease-in text-yellow-500 hover:translate-x-2 inline-flex items-center md:mb-2 lg:mb-0"
              href={props.link}
            >
              {props.view}
              <svg
                class="w-4 h-4 ml-2"
                viewBox="0 0 24 24"
                stroke="currentColor"
                stroke-width="2"
                fill="none"
                stroke-linecap="round"
                stroke-linejoin="round"
              >
                <path d="M5 12h14"></path>
                <path d="M12 5l7 7-7 7"></path>
              </svg>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Project_card;
