import React from "react";
import vs3 from "../Images/VScode3.png";

function VS_CODE() {
  return (
    <>
      <section className="pt-2 px-3">
        <div className="font-mono p-8 my-2 border-solid glass-effect bg-transparent-dark-gray border-gray-800 border-2 rounded-md flex flex-wrap md:flex-nowrap">
          <div className="md:flex-grow">
            <h1 className="text-3xl font-medium text-blue-300 title-font mb-2 underline underline-offset-2">
              VS Code Installation
            </h1>
            <p className="leading-relaxed text-white font-mono">
              Hello!, Coders and Developers, In this Blog I shall guide you on
              how to install VS code on your System. VS code is an editor and
              IDE that is used to write code. It is a free and open source
              software developed by Microsoft. For further details *
              <a
                href="https://code.visualstudio.com/docs"
                target="_blank"
                className="text-red-400"
              >
                VS Code/Docs
              </a>
              *. If you want to give a try on VS code without installation, *
              <a
                target="_blank"
                href="https://vscode.dev/"
                className="text-red-400"
              >
                VS Code/Web
              </a>
              *
            </p>
            <br />
            <p className="leading-relaxed text-lime-500 font-mono">
              To Install VS code on your system, download the VS code installer
              from the website{" "}
              <a
                href="https://code.visualstudio.com/"
                target="_blank"
                className="text-blue-400"
              >
                link
              </a>
              .
            </p>
            <p className="leading-relaxed text-white font-mono">
              <br />
              After downloading the{" "}
              <span className="text-blue-400">installer </span>, Run the{" "}
              <span className="text-blue-400">VS Code </span> Installer. As you
              go on, Read the{" "}
              <span className="text-blue-400">License Agreement</span>, click on
              the <span className="text-blue-400">"I Agree"</span> button.
              <br />
              <br />
              Next, It will ask you in which location you want to install VS
              code. By default it will be selected to C drive and Click on{" "}
              <span className="text-blue-400">"Next" </span>button.
              <br />
              <br />
              Then it will ask about{" "}
              <span className="text-blue-400">Additional Tasks</span>. Tick on{" "}
              <span className="text-blue-400">"Create Desktop Icon"</span> if
              you want the Application on Desktop folder. And don't forget to
              tick on{" "}
              <span className="text-blue-400">
                "Create Start Menu Shortcut"
              </span>{" "}
              as it is really helpful in opening Program Codes faster. And then
              Install VS code.
            </p>
            <br />
            <p className="leading-relaxed text-lime-500 font-mono">
              After Installation, you can open VS code by
            </p>
            <p className="leading-relaxed text-white font-mono">
              1) <span className="text-red-400">Double Click</span> on the VS
              Code icon on your Desktop.
              <br />
              2) <span className="text-red-400">Right Click</span> on the Folder
              and select "Open with VS Code"
              <br />
              3) By typing <span className="text-red-400">"code"</span> in the
              command prompt, you can open VS code.
            </p>
            <br />
            <p className="leading-relaxed text-lime-500 font-mono">
              This is how your VS Code looks.
            </p>
            <img
              class="w-full object-cover object-center "
              alt="VS Code look"
              src={vs3}
            />
            <br />
            <p className="leading-relaxed text-lime-500 font-mono">
              As we have successfully installed, Now, it is your turn to code
              and develop application for the better future.
            </p>
            <br />
          </div>
        </div>
      </section>
    </>
  );
}

export default VS_CODE;
