import React from "react";
import { Link } from "react-router-dom";

function Blog_card(props) {
  return (
    <div className="p-8 my-2 border-solid glass-effect bg-transparent-dark-gray border-gray-800 border-2 rounded-md flex flex-wrap md:flex-nowrap">
      <div className="md:w-64 md:mb-0 mb-6 flex-shrink-0 flex flex-col">
        <span className="font-semibold title-font text-blue-200 underline underline-offset-2">
          {props.category}
        </span>
        <span className="mt-1 text-lime-400 text-sm">{props.date}</span>
      </div>
      <div className="md:flex-grow">
        <h2 className="text-2xl font-medium text-blue-300 title-font mb-2 underline underline-offset-2">
          {props.title}
        </h2>
        <p className="leading-relaxed text-white">{props.content}</p>
        <Link
          className="transition-all duration-100 ease-in text-yellow-400 inline-flex items-center mt-4 hover:translate-x-2"
          to={props.link}
        >
          {props.view}
          <svg
            className="w-6 h-6 ml-2"
            viewBox="0 0 24 24"
            stroke="currentColor"
            stroke-width="2"
            fill="none"
            stroke-linecap="round"
            stroke-linejoin="round"
          >
            <path d="M5 12h14"></path>
            <path d="M12 5l7 7-7 7"></path>
          </svg>
        </Link>
      </div>
    </div>
  );
}

export default Blog_card;
